.ts-checkout {

    &__content {

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin: 8px 0;
        min-height: 120px;
        transition: all 0.3s ease-in-out;
        @media screen and (min-width: $media--desktop) {
            padding: 12px 16px;
            min-height: 230px;
            background: $color-white;
            border: 1px solid $border-color;
            border-radius: $border-radius;
            cursor: pointer;

            &:first-child {
                margin-top: 0; } }



        &:hover {
            border-color: $color-primary; }

        &:active {
            background: $color-primary--light; } }



    &__code {
        color: $color-muted;
        font-size: $font-size;
        font-weight: 400;
        line-height: 18.96px;
        text-align: right; } }

.ts-checkout-info-passenger {

    &__title {
        font-size: $font-size;
        font-weight: 400;
        line-height: 21.33px;
        text-align: left;
        text-transform: uppercase;
        margin-top: 10px;
        @media screen and (min-width: $media--desktop) {
            font-size: $font-size--lg; } }

    &__code {
        margin-top: 10px;
        color: $color-muted;
        font-size: $font-size;
        font-weight: 400;
        line-height: 18.96px;
        text-align: right;

        @media screen and (min-width: $media--desktop) {
            text-align: right;
            margin-top: 10px;
            font-size: 14px; } } }

.ts-checkout-info-company {
    margin-top: 10px;

    &__title {
        font-size: $font-size;
        font-weight: 400;
        line-height: 21px;
        text-align: left;

        @media screen and (min-width: $media--desktop) {
            font-size: $font-size--lg; } }


    &__code {
        color: $color-muted;
        font-size: 14px;
        font-weight: 400;
        line-height: 18.96px;
        text-align: left;

        @media screen and (min-width: $media--desktop) {
            text-align: right;
            font-size: $font-size; } } }

